import { appAction } from "app/context/app.actions"

export const INITIAL_STATE = {
  menuVisible: false,
  user: {
    name: "Dejan Bratic",
    title: "Software Engineer",
    contact: {
      email: "dejobratic@gmail.com",
      socialNetworks: [
        {
          type: "linkedin",
          url: "https://www.linkedin.com/in/dejobratic",
        },
        {
          type: "github",
          url: "https://github.com/dejobratic",
        },
        {
          type: "twitter",
          url: "https://twitter.com/dejobratic",
        },
      ],
    },
    about: "",
    workExperience: [
      {
        company: "",
        start: "",
        end: "",
      },
    ],
    projects: [
      {
        title: "CRWN Clothing",
        demoUrl: "",
      },
      {
        title: "ProShop",
        demoUrl: "",
      },
    ],
  },
}

const appReducer = (state = INITIAL_STATE, action) => {
  switch (action.type) {
    case appAction.SHOW_MENU:
      return { ...state, menuVisible: true }

    case appAction.HIDE_MENU:
      return { ...state, menuVisible: false }

    default:
      return state
  }
}

export default appReducer
